@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* All of the following is from Net Ninja, except that I added footer bits and changed some colors************ */

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar, .footer {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1, h2 {
  color: lightslategray;
}
.navbar .links, .footer .links {
  margin-left: auto;
}
.navbar a, .footer a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover, .footer a:hover {
  color: red;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

.contact {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.contact label {
  text-align: left;
  display: block;
}
.contact h2 {
  font-size: 20px;
  color: lightslategray;
  margin-bottom: 30px;
}
.contact input, .contact textarea {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.contact button {
  background: lightslategray;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}


/* Something I added */

.invalid {
  color: red;
  font-weight: bolder;
}
